body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

select {
  /* styling */
  background-color:  #161b24;
  color: white;
  display: inline-block;
  font: inherit;
  padding: 0.5em 2em 0.2em .5em;
  border-radius: 5px;

  /* reset */
  margin: 0;
  box-sizing: border-box;
}
.App {
  text-align: center;
  background-color: #11151c;
}

.select-col {
  background-color: #161b24;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.hide {
  -webkit-animation: fadeinout 0.2s linear forwards;
          animation: fadeinout 0.2s linear forwards;
}

.show {
  -webkit-animation: display 0.3s linear forwards;
          animation: display 0.3s linear forwards;
}

@-webkit-keyframes fadeinout {
  0% {
    opacity: 0.6;
  }
  50% {
    opacity: 0.2;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fadeinout {
  0% {
    opacity: 0.6;
  }
  50% {
    opacity: 0.2;
  }
  100% {
    opacity: 0;
  }
}

@-webkit-keyframes display {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 0;
  }
  50% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}

@keyframes display {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 0;
  }
  50% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}
.pokemon-button {
    margin-top: 5;
    padding: 5;
    background-color: rgba(255, 255, 255, 0);
    font-size: 20;
    color: white;
    width: 200px;
    border-width: 0px;
    /* border-radius: 10; */
    /* border: 2px solid #32a887; */
    display: inline-block;
    transition: all .5s ease
}

.pokemon-button:hover {
    background-color: #32a887;
    color: #11151c;
}

.pokemon-button:active {
    background-color: #1b765d;
    transition: none;
}

.pokemon-button td {
    display: inline-flex;
    padding-left:5px;
    align-items: right;
}

.party-member-container {
    height:100px;
    width:100px;
    border-radius: 50%;
    position: relative;
    margin-left: 5px;
    margin-right: 5px;
    display: inline-block;
}

.party-member-background {
    background-color: #161b24;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    border-radius: 50%;
    z-index: 0;
    outline: 2px solid #262f42;
}

.party-member-icon {
    display: inline-block;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.party-member-remove {
    background-color: #ea5d5d;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    z-index: 2;
    border-radius: 50%;
    border: none;
}

.party-member-remove:hover {
    opacity: .5;
    transition: all .5s ease;
}

.action-button {
    background-color: #ea5d5d;
    height: 50px;
    width: 125px;
    border: none;
    color: white;
    font-weight: bold;
    border-radius: 5px;
}

.stat-view {
    background-color: #32a887;
    border: 2px solid #1b765d;
    border-radius: 5px;    
}

.stat-bar {
    transition: width .5s ease
}
